.sidebar {
  flex-basis: 23%;
  display: -ms-flex;
  width: 23%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-align: start;
  align-items: flex-start;
  position: fixed;
  padding-left: 35px;
  padding-top: 3%;
  overflow: visible;
  height: 80vh;
  z-index: 10;
  right: 0;
  transition: 0.3s;

  @media(max-width: @screen-md) {
    padding-top: 3%;
    flex-basis: 30%;
    width: 30%;
    transition: 0.3s;
  }
  @media(max-width: @screen-xd) {
    right: -30%;
    background-color: @fake-white-color;
    transition: 0.3s;
  }
  @media(max-width: @screen-sm) {
    width: 100%;
    height: 110%;
    right: -100%;
    transition: 0.3s;
    padding-left: 45px;
    padding-right: 35px;
  }
  &--visible {
    @media(max-width: @screen-xd) {
      right: 0;
      transition: 0.3s;
    }
    @media(max-width: @screen-sm) {
      right: 0;
      transition: 0.3s;
    }
    .sidebar__burger {
      @media(max-width: @screen-sm) {
        right: auto;
        left: 0;
        transition: 0.3s;
      }
    }
  }

  &:after {
    content: '';
    display: inline-block;
    height: 200%;
    width: 200%;
    position: absolute;
    top: 50%;
    right: -900%;
    transform: translate(-50%, -50%);
    background: linear-gradient(to right, rgba(245, 245, 245, 0) 0%, #f5f5f5 70%);
    z-index: 0;
  }

  &:before {
    content: '';
    display: inline-block;
    height: 120vh;
    width: 2px;
    background-color: @light-gray-color;
    position: absolute;
    left: 0;
  }
  &__burger {
    display: none;

    @media(max-width: @screen-xd) {
      display: block;
      .text(@fake-white-color, 1em);
      padding: 10px 10px 10px 10px;
      position: absolute;
      right: 100%;
      text-align: center;
      font-weight: 600;
      top: 10%;
      background-color: #212121;
      transition: 0.3s;
    }
  }
