  //     width: 30px;
  //     background-color: #323232;
  //     position: absolute;
  //     right: 0;
  //     top: -8px;
  //     transition: 0.3s;
  //   }
  // }
  &__list {
    position: absolute;
    z-index: 10;
  }
  &__item {
    list-style-type: none;
    position: relative;
    z-index: 10;

    &:hover .submenu1st {
      display: block;
    }
  }
  .submenu1st .submenu__item:hover .submenu2nd {
    display: block;
  }
  &__link {
    .text(@black-color, 0.8em);
    // white-space: nowra p;
    line-height: 26px;

    &:hover {
      color: @blue-color;
      text-decoration: underline;
    }
  }

}
.sidebar::-webkit-scrollbar { width: 0px; height: 0px;}
.sidebar::-webkit-scrollbar-button { display: none; background-color: transparent; }
.sidebar::-webkit-scrollbar-track {  background-color: transparent;}
.sidebar::-webkit-scrollbar-track-piece { background-color: transparent;}
.sidebar::-webkit-scrollbar-thumb { height: 70px; background-color: transparent; border-radius: 0px;}
