//     : @import './src/less/blocks/tarif.less';


.tarif {
  flex-basis: 25%;

  @media(max-width: @screen-sm) {
    flex-basis: 50%;
  }

  &__title {
    .text(@black-color, 1.6em);
    text-transform: uppercase;
    font-weight: 700;
    line-height: 1;
    width: 100%;
    text-align: center;
    @media(max-width: @screen-sd) {
      font-size: 1.0em;
    }
    @media(min-width: @screen-sd) and (max-width: @screen-md) {
      font-size: 1.2em;
    }
    @media(max-width: @screen-sm) {
      font-size: 0.8em;
    }


  }
  &__descr-wrapper {
    width: 100%;
    text-align: center;
    padding: 50px 0;

    @media (max-width: @screen-md) {
      padding: 5% 0;
    }

  }
  &__price {
    .text(@black-color, 3.2em);
    text-transform: uppercase;

    @media(max-width: @screen-md) {
      font-size: 2.0em;
    }
    @media(max-width: @screen-sm) {
      font-size: 1.5em;
    }
  }
  &__descr {
    .text(@black-color, 1.2em);
    text-transform: uppercase;

    @media(max-width: @screen-md) {
      font-size: 0.8em;
    }
    @media(max-width: @screen-sm) {
      font-size: 0.6em;
      width: 100%;
      text-align: center;
    }
  }
  &__list {
    padding-left: 15px;
    @media (max-width: @screen-md) {
      padding-left: 30px;
    }
  }
  &__item {
    .text(@black-color, 0.8em);
    text-transform: uppercase;
    line-height: 3;
    padding-left: 45px;

    @media(min-width: @screen-sd) and (max-width: @screen-md) {
      padding-left: 0;
      font-size: 0.7em;
    }
    @media(max-width: @screen-sd) {
      font-size: 0.6em;
      padding-left: 0;
    }
    @media(max-width: @screen-sm) {
      line-height: 2;
    }
    &:before {
      content: '';
      display: inline-block;
      width: 35px;
      height: 35px;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background-repeat: no-repeat;
      background-size: contain;
      @media (max-width: @screen-md) {
        width: 25px;
        height: 25px;
        left: -25px;
      }
    }
    &--yes {
      &:before {
        background-image: url('/img/tarif_yes.png');
      }
    }
    &--no {
      &:before {
        background-image: url('/img/tarif_no.png');
      }
    }
  }





  &__wrapper {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: row;
    padding: 0 2%;
    justify-content: space-between;
    width: 100%;

    @media(min-width: @screen-md) and (max-width: @screen-md) {
      padding: 0 0%;
    }
    @media(max-width: @screen-sd) {
      padding: 10% 0;
    }
    @media(max-width: @screen-sm) {
      flex-wrap: wrap;
      padding: 0% 0;
    }
  }
}
