//     : @import './src/less/blocks/title.less';


.title {
  .text(@black-color, 1.5em);
  text-transform: uppercase;
  font-weight: 600;
  margin-right: auto;
  min-width: 1px;
  min-height: 1px;

  span {
    color: @gray-color;
  }

  &__wrapper {
    flex-basis: 50%;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: flex-end;
    @media (max-width: @screen-md) {
      flex-basis: 40%;
    }
    @media(max-width: @screen-xd) {
      flex-basis: 34%;
    }
    @media(max-width: @screen-sd) {
      flex-basis: 30%;
    }
    @media(max-width: @screen-sm) {
      display: none;
    }
    .block__btn {
      margin-top: 0;
      margin-left: auto;
      margin-left: 10px;
      width: 140px;

      @media (max-width: @screen-md) {
        width: 110px;
        font-size: 0.8em;
      }
      @media(max-width: @screen-sd) {
        width: 60px;
        font-size: 0.6em;
        height: 30px;
        line-height: 29px;
        padding-left: 10px;
      }

      &:last-of-type {
        background-color: @gray-color;
        width: 190px;
        @media (max-width: @screen-md) {
          width: 150px;
        }
        @media(max-width: @screen-sd) {
          width: 100px;
        }
        &:hover {
          background-color: @deep-gray-color;
        }
      }
    }
  }


}
