//     : @import './src/less/blocks/logo.less';


.logo {
  text-decoration: none;
  flex-basis: 50%;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: row;
  align-items: center;
  -webkit-filter: grayscale(1);
  filter: gray;
  filter: grayscale(1);

  &:hover {
    -webkit-filter: grayscale(0);
    filter: grayscale(0);
  }

  @media (max-width: @screen-md) {
    flex-basis: 25%;
  }
  @media(max-width: @screen-xd) {
    flex-basis: 30%;
  }
  @media(max-width: @screen-sm) {
    flex-basis: 60%;
  }

  &__img {
    margin-right: 15px;
    width: auto;
    height: 90%;
    @media(max-width: @screen-sm) {
      height: 25%;
      width: 25%;
    }
  }

  &__title {
    .text(@deep-blue-color, 1.6em);
    font-weight: 900;
    text-transform: uppercase;
    display: block;

    span:first-of-type {
      color: @blue-color;
    }
    span:nth-of-type(2) {
      color: @deep-blue-color;
    }
    span:last-of-type {
      color: @dark-blue-color;
    }
    @media(max-width: @screen-sd) {
      font-size: 1.2em;
    }
    @media(max-width: @screen-sm) {
      font-size: 1.0em;
    }
  }
  &__text {
    .text(@black-color, 0.8em);
    text-transform: uppercase;
    font-weight: 300;
    display: block;
    @media(max-width: @screen-sd) {
      font-size: 0.6em;
    }
  }
}
