//     : @import './src/less/blocks/block.less';


.block {
  height: 80vh;
  display: inline-block;
  width: 100%;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-top: 6%;
  padding-bottom: 5%;
  @media(max-width: @screen-md) {
    width: 99%;
  }
  @media(max-width: @screen-xd) {
    width: 100%;
    padding-top: 6%;
  }
  &:first-of-type {
    @media(max-width: @screen-xd) {
      padding-top: 18%;
    }
  }
  &--how {
    justify-content: flex-end;
  }

  &--404 {
    justify-content: flex-end;
    &__main-title {
      &:before {
        background-image: url('/img/404_bgr.png');
      }
    }
  }



  &--result .block__item {
    display: block;
    // white-space: nowrap;
    margin-bottom: 10px;
    text-indent: 0;
  }
  &--result .block__text--bold {
    height: 55px;
    @media(max-width: @screen-sm) {
      height: 10%;
    }
  }

  &__main-title {
    .text(@fake-white-color, 16.25em);
    font-weight: 700;
    line-height: 175px;
    height: 175px;
    width: 100%;
    text-align: left;

    @media(max-width: @screen-md) {
      font-size: 11.25em;
      line-height: 115px;
      height: 115px;
    }
    @media(max-width: @screen-sm) {
      font-size: 7.5em;
      line-height: 83px;
      height: 83px;
    }

    &:before {
      content: '';
      display: inline-block;
      height: 100%;
      background-image: url('/img/main_bgr.png');
      background-repeat: no-repeat;
      width: 110%;
      position: absolute;
      z-index: -1;
      right: 0;
      top: 0;
      background-size: cover;
      background-position: center;
    }
  }

  &__btn {
    .btn(240px, @black-color, @gray-color, @blue-color);
    height: 40px;
    line-height: 39px;
    font-size: 1em;
    margin-top: 40px;
    text-transform: uppercase;
    text-align: left;
    padding-left: 20px;
    font-weight: 600;

    @media(max-width: @screen-md) {
      margin-top: 20px;
    }
    @media(max-width: @screen-sm) {
      width: 120px;
      font-size: 0.6em;
      height: 30px;
      line-height: 29px;
    }
  }

  &__logo-wrapper {
    height: 65px;
    width: 100%;
    position: relative;
    margin-bottom: 30px;
    @media(max-width: @screen-sm) {
      display: none;
    }
  }

  &__logo {
    position: absolute;
    top: 50%;
    left: 20%;
    transform: translate(-50%, -50%);
    width: auto;
  }

  &__title {
    .text(@black-color, 1.6em);
    text-transform: uppercase;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 7%;

    @media(max-width: @screen-md) {
      font-size: 1.4em;
    }
    @media(max-width: @screen-xd) {
      font-size: 1.0em;
    }
    @media(max-width: @screen-sm) {
      font-size: 0.8em;
    }
  }

  &__text {
    .text(@black-color, 0.8em);
    display: block;

    &--bold {
      font-weight: 600;
      height: 12%;

      @media(max-width: @screen-md) {
        height: 10%;
      }
    }
    @media(max-width: @screen-sm) {
      font-size: 0.6em;
      height: 3%;
    }

    &--main {
      @media(max-width: @screen-sm) {
        height: 15%;
      }
    }
  }

  &__item {
    text-indent: 15px;
    list-style-type: none;
    .text(@black-color, 0.8em);
    margin-bottom: 3%;
    line-height: 1.5;

    @media(max-width: @screen-md) {
      margin-bottom: 15px;
    }
    @media(max-width: @screen-sm) {
      font-size: 0.6em;
      line-height: 1.2;
      text-indent: 0;
    }

    a {
      .text(@black-color, 1em);
      font-weight: 600;
      text-decoration: none;
      @media(max-width: @screen-xd) {
        font-size: 0.8em;
      }
    }
  }

  &__col {
    width: 50%;
    padding-left: 8%;
    padding-right: 3%;
    position: relative;

    @media(max-width: @screen-md) {
      padding-left: 4%;
      padding-right: 2%;
    }
    @media(max-width: @screen-sm) {
      width: 100%;
      padding: 0;
    }
    // height: 100%;

    &--how {
      width: 41%;
      padding-left: 6%;
      // padding-right: 1%;
      @media(max-width: @screen-sm) {
        padding: 0 1%;
      }

      .block__item:before {
        content: '';
        display: inline-block;
        width: 4px;
        height: 4px;
        background-color: @black-color;
        position: absolute;
        top: 8px;
        left: 0;
        @media(max-width: @screen-sm) {
          display: none;
        }
      }
      .block__text--bold {
        @media(max-width: @screen-sm) {
          height: 11%;
        }
      }
      @media(max-width: @screen-sm) {
        width: 50%;
      }
    }

    &--contact {
      .block__item {
        width: 100%;
        margin-bottom: 5px;
        text-indent: 0;

        &:before {
          display: none;
        }
      }
      .block__list {
        margin-bottom: 30px;
      }
      .block__text {
        font-weight: 600;
        margin-top: 30px;
      }
    }

    &--pic {
      height: 83%;
      padding-right: 0;

      @media(max-width: @screen-xd) {
        height: 100%;
      }
      &:before {
        content: '';
        display: inline-block;
        height: 100%;
        width: 100%;
        background-image: url('/img/how_bgr.png');
        position: absolute;
        top: 0;
        right: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: right;
        @media(max-width: @screen-sm) {
          display: none;
        }
      }
    }
  }
}
