//     : @import './src/less/blocks/footer-list.less';


.footer-list {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: row;
  align-items: baseline;
  flex-wrap: wrap;

  @media(max-width: @screen-sd) {
    width: 40%;
  }
  @media(max-width: @screen-sm) {
    display: none;
  }


  &__item {
    list-style-type: none;

    p {
      .text(@black-color, 0.8em);
      margin-right: 15px;
    }
    a {
      .text(@black-color, 0.8em);
      margin-right: 15px;
      @media(max-width: @screen-xd) {
        // font-size: 12px;
        margin-right: 0;
      }
    }
    @media(max-width: @screen-sd) {
      font-size: 14px;
    }
    @media(max-width: @screen-xd) {
      font-size: 14px;
      margin-right: 5px;
    }
  }
}
