//     : @import './src/less/blocks/main.less';


.main {
  // margin-top: 40px;
  // margin-bottom: 60px;
  height: 80vh;
  // overflow: hidden;
  @media(max-width: @screen-sm) {
    height: 70vh;
  }

  &__wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__scroll {
    flex-basis: 80%;
    // overflow-x: scroll;
    // height: 80vh;
    @media(max-width: @screen-md) {
      flex-basis: 73%;
      width: 73%;
    }
    @media(max-width: @screen-xd) {
      flex-basis: 100%;
      width: 100%;
    }
  }

  &__inner {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
    flex-wrap: nowrap;
    height: 100%;
  }

}

.main__scroll::-webkit-scrollbar { width: 0px; height: 0px;}
.main__scroll::-webkit-scrollbar-button { display: none; background-color: transparent; }
.main__scroll::-webkit-scrollbar-track {  background-color: transparent;}
.main__scroll::-webkit-scrollbar-track-piece { background-color: transparent;}
.main__scroll::-webkit-scrollbar-thumb { height: 70px; background-color: transparent; border-radius: 0px;}
