.page-header {
  padding: 2% 0;
  height: 10vh;
  z-index: 100;
  background: linear-gradient(to top, rgba(245, 245, 245, 0) 0%, #f5f5f5 20%);

  @media(max-width: @screen-sd) {
    background: -webkit-linear-gradient(bottom, rgba(245, 245, 245, 0) 0%, #f5f5f5 0%);
    background: -o-linear-gradient(bottom, rgba(245, 245, 245, 0) 0%, #f5f5f5 0%);
    background: linear-gradient(to top, rgba(245, 245, 245, 0) 0%, #f5f5f5 0%);
  }

  &__inner {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
