//     : @import './src/less/blocks/feedback.less';


.feedback {
  padding: 70px 0;
  position: relative;
  left: -15px;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;

  @media(max-width: @screen-sm) {
    padding: 40px 0;
  }

  .block__text {
    @media(max-width: @screen-sm) {
      height: 3%;
    }
  }
  .block__btn {
    margin-top: 60px;
    @media(max-width: @screen-xd) {
      font-size: 0.8em;
      width: 230px;
    }
    @media(max-width: @screen-sm) {
      width: 100%;
    }
  }

  &__input {
    width: 100%;
    height: 37px;
    line-height: 37px;
    background-color: @fake-white-color;
    border: 0;
    border-bottom: 1px solid @gray-color;
    padding-left: 15px;
    outline: none;

    @media(max-width: @screen-xd) {
      font-size: 0.8em;
    }
  }
}
