//     : @import './src/less/blocks/city.less';


.city {
  text-align: right;
  flex-basis: 30%;
  // padding-right: 120px;
  padding-left: 35px;

  @media (max-width: @screen-md) {
    flex-basis: 34%;
  }
  @media(max-width: @screen-xd) {
    flex-basis: 40%;
  }
  @media(max-width: @screen-sd) {
    flex-basis: 45%;
  }
  @media(max-width: @screen-sm) {
    flex-basis: 40%;
  }
  &__wrapper {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;

    @media(max-width: @screen-sm) {
      flex-wrap: wrap;
    }
  }

  &__name {
    .text(@black-color, 0.8em);
    margin-left: 20px;
    margin-right: 5px;
    text-transform: uppercase;

    @media(max-width: @screen-sd) {
      font-size: 0.6em;
    }
    @media(max-width: @screen-sm) {
      margin: 0;
    }
  }

  &__btn {
    .btn(120px, @black-color, @gray-color, @blue-color);
    height: 40px;
    line-height: 39px;
    font-size: 1em;
    text-transform: uppercase;
    text-align: left;
    padding-left: 20px;
    font-weight: 500;
    &:last-of-type {
      background-color: @gray-color;
    }
  }

  &__select {
    background-color: transparent;
    border: 0;
    .text(@black-color, 0.8em);
    text-transform: uppercase;
    background-image: none;

    @media(max-width: @screen-sd) {
      font-size: 0.6em;
    }

    option {
      background: transparent;
    }
  }
}
